body.active-modal {
  overflow-y: hidden;
}

.modal, .overlay {
  width: 100vw;
  height: 100vh;
  position: fixed;
  inset: 0;
}

.overlay {
  background: #313131cc;
}

.modal-content {
  background: #f1f1f1;
  border-radius: 3px;
  min-width: 300px;
  max-width: 600px;
  padding: 14px 28px;
  line-height: 1.4;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
}
